<template>
    <header class="header">
        <img src="/static/img/arrow-up.svg" @click="back"/>
        <span>{{ obj.title }}</span>
    </header>
    <span v-if="obj.subTitle != null" class="sub-title">{{ obj.subTitle }}</span>
    <div id="md" for="html-export">
        <div class="markdown-preview flex" id="show" v-html="html"></div>
    </div>
    <img src="/static/img/arrow-up.svg" class="btn-up" @click="this.$toTop" v-if="html != ''">
    <div id="modal" ref="modal" style="display:none" @click="closeModal"><img /></div>
    <StatusLayout ref="sl"/>
</template>

<script>
import StatusLayout from '../components/StatusLayout.vue'
export default {
    name: 'Show',
    components:{
        StatusLayout
    },
    data(){
        return {
            obj :{},
            md  :"",
            html:""
        }
    },
    methods:{
        
        showArticle(fileId){
            this.$refs.sl.loading = true
            this.filterArticle(fileId)
            this.getMarkdown(`https://md.jqwong.cn/technology/${fileId}.md`)
        },

        // 过滤ArticleList
        filterArticle(fileId){
            this.$store.state.technology.atList.forEach(year => {
                if(fileId >= year[year.length-1].fileId && fileId <= year[0].fileId){
                    year.forEach(item => {
                        if(item.fileId == fileId){
                            this.obj = item
                        }
                    })
                }
            })
        },


        showNote(fileId){
            this.$refs.sl.loading = true
            this.filterNoteList(fileId)
            this.getMarkdown(`https://md.jqwong.cn/note/${fileId}.md`)
        },

        // 过滤NoteList
        filterNoteList(fileId){
            this.$store.state.note.noteList.forEach(year => {
                if(fileId >= year[year.length-1].fileId && fileId <= year[0].fileId){
                    year.forEach(item => {
                        if(item.fileId == fileId){
                            this.obj = item
                        }
                    })
                }
            })
        },

        // 请求Markdown
        getMarkdown(url){
            this.$http.get(url)
            .then(res => {
                let reg = /\*\* .* []?\*\*/g
                res = res.data.replaceAll(reg,(match) => {
                    match = match.replaceAll("** ","**")
                    match = match.replaceAll(" **","**")
                    return match
                })
                res = res.replaceAll("\\'","'")
                res = res.replaceAll("\\\"","\"")
                res = res.replaceAll("\\<","<")
                this.md = res
                this.compile(this.md)
            })
            .catch(error => {
                console.log(error)
                this.$refs.sl.error = true
            })
        },

        // 解析
        compile(md){
            this.html = marked(md)
            setTimeout(() => {
                this.lineNum()
                this.cb()
                this.pic()
                this.$refs.sl.loading = false
            },500)
        },

        // 行号
        lineNum(){
            var pre = document.getElementsByTagName("pre")
            for(let i = 0; i < pre.length; i++){
                if(pre[i].childNodes[0].tagName == "CODE"){
                    let w_number = 7
                    let reg = /\n/ig
                    let data = pre[i].childNodes[0].childNodes[0].data
                    let match = data.match(reg)
                    data = data.substring(0,data.lastIndexOf("\n"))
                    data = data.replaceAll("<","&lt;")
                    if(match != null){
                        w_number = w_number * (String(match.length).length)
                    }
                    data = `<span class='line-number' style='width:${w_number}px'>1</span>` + data
                    let index = 1
                    data = data.replaceAll("\n",function(match, p1, p2, p3, offset, string){
                        index++
                        return `\n<span class='line-number' style='width:${w_number}px'>${index}</span>`
                    })
                    pre[i].childNodes[0].innerHTML = data
                }
            }
        },

        // 复选框
        cb(){
            var input = document.getElementsByTagName("input")
            for(let i = 0; i < input.length; i++){
                switch(input[i].getAttribute("type")){
                case "checkbox":
                    input[i].setAttribute("disabled","")
                    if(input[i].parentNode != null && input[i].parentNode.tagName == "LI"){
                        input[i].parentNode.className = "task-list-item"
                    }
                    break;
                }
            }
        },

        // 查看图片
        pic(){
            var imgs = document.getElementById("show").querySelectorAll("img")
            const _this = this
            for(let i = 0; i < imgs.length; i++){
                imgs[i].onclick = function(){
                    _this.$refs.modal.style.display = ""
                    _this.$refs.modal.children[0].setAttribute('src',this.getAttribute('src'))
                }
            }
        },

        // 关闭模态框
        closeModal(){
            this.$refs.modal.style.display = "none"
        },

        // 返回
        back(){
            window.history.go(-1)
        }
    },
    mounted(){
        let obj = this.$route.query
        switch(obj.type){
            case this.$comm.type_article:
                this.showArticle(obj.fileId)
                break
            case this.$comm.type_note:
                this.showNote(obj.fileId)
                break    
        }
    }
}
</script>

<style scoped>
    
    .header{
        height: 55px;
        line-height: 55px;
        display: flex;
        flex-direction: row;
        margin-top: 45px;
        font-size: 28px;
    }

    .header>img{
        width:30px;
        transform:rotate(-90deg);
        margin-right: 35px;
        cursor: pointer;
    }

    .header>span{
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }

    .sub-title{
        position: absolute;
        top: 0;
        right: 15px;
        left: 15px;
        text-align: center;
        margin-top: 10px;
        font-size: 12px;
    }
</style>